import { AccessiblePageType } from '@/models/access-control';
import {
  PhHouse,
  PhKanban,
  PhScroll,
  PhTimer,
  PhUserList,
  PhWebhooksLogo,
} from '@phosphor-icons/vue';

export const workflowMetadata = {
  stages: [
    {
      icon: PhUserList,
      typeName: 'forms',
      description: 'Wait for a user input',
      key: 'F',
      title: 'Forms',
    },
    {
      typeName: 'hooks',
      title: 'Hooks',
      icon: PhWebhooksLogo,
      description: 'Wait for a trigger from an external service',
      key: 'H',
    },
    {
      typeName: 'jobs',
      title: 'Jobs',
      icon: PhTimer,
      description: 'Executed periodically',
      key: 'J',
    },
    {
      typeName: 'scripts',
      title: 'Tasklets',
      icon: PhScroll,
      description: 'Run a script when a task arrives',
      key: 'T',
    },
  ],
} as const;

export type WorkflowTaskTransition = {
  type: 'task';
  props: {
    taskType: string | null;
  };
};

export type WorkflowTransitionType = WorkflowTaskTransition['type'];

type WorkflowMetadata = typeof workflowMetadata;
export type WorkflowStageType = WorkflowMetadata['stages'][number]['typeName'];

type WorkflowStageTypeSingular = 'form' | 'hook' | 'job' | 'script' | 'tasklet';
export function stageIcon(stageType: WorkflowStageType | WorkflowStageTypeSingular) {
  if (stageType === 'tasklet') stageType = 'script';
  const stageMetadata = workflowMetadata.stages.find(
    (s) => s.typeName === stageType || s.typeName === `${stageType}s`,
  );
  if (!stageMetadata) throw new Error(`No metadata found for stage ${stageType}`);
  return stageMetadata.icon;
}

export const iconPath = (type: AccessiblePageType) => {
  if (type === 'kanban') return PhKanban;
  if (type === 'home') return PhHouse;
  return stageIcon(type);
};
